.navBarTitle {
    flex-grow: 1;
    color: white;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: black;
    font-family: 'Roboto', sans-serif;
    font-size: 50px;
    font-weight: 600;
    margin-top: 10px;
}
.navButtonBar {
    margin-left: auto;
    padding-top: 10px;
}
.tabsSize {
    font-size: 100px;
}
.spacers {
    padding-right: 10px;
}
.customizeNavBar {
    min-height: 0px;
    display: flex;
    flex-direction: row;
    padding-left: 24px;
    padding-right: 24px;
}

@media screen and (max-width: 768px) {
    .navButtonBar {
        margin-left: 0px;
        padding-top: 0px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    .customizeNavBar {
        min-height: 0px;
        display: flex;
        flex-direction: column;
        padding-left: 0px;
        padding-right: 0px;
    }
    .navDCSCLogo {
        align-self: center;
    }
}